import { AppUrls, isPreview, isSocialWall } from "../../../../actions/api";
import { TagembedUrl } from "../../../../actions/api";
import { tagembedUrl } from "../../../../constants"; 
import { onWindowEvent } from "../../../../utils/Common";

const ErrorButtons = ({ editor, wall,error_code }) => {
    const isTagEmbedStatus = wall?.User?.userDatabaseType == "tagembed" || tagembedUrl
    return <div className="tb_error_page_actions">
        <div className="tb_error_action_list">
            {!editor && !isPreview ? <a href={`${isTagEmbedStatus ? TagembedUrl : AppUrls}`} target={'_blank'} className="tb_error_btn_primary">{isSocialWall ? `Add Another Feed` : `Access Now`}</a> : <>
            {   error_code == 213 ? <button onClick={() => onWindowEvent(5)} className="tb_error_btn_primary">Update Now</button> : <><button onClick={() => onWindowEvent(2)} className="tb_error_btn_primary" style={{ marginRight: '10px' }}>Add Another Feed</button>
                        <button onClick={() => onWindowEvent(1)} className="loadDemoWall tb_error_btn_secondary">Show Demo Posts</button></>
                }
            </>}
        </div>
    </div>
}
export default ErrorButtons