import ErrorBranding from "../Elements/ErrorBranding";
import ReactHtmlParser from 'react-html-parser';
import ErrorButtons from "./ErrorButtons";
const ErrorMessage = ({ editor, title, description, error_code,wall }) => {
  return <div className="tb_error_page_wrapper">
    <div className="tb_error_content">
      <div className="tb_error_title">{title}</div>
      <div className="tb_error_des">{ReactHtmlParser(description)}</div>
      <ErrorButtons editor={editor} wall={wall} error_code={error_code} />
    </div>
    {!editor ? <ErrorBranding wall={wall} /> : null}
  </div>
}
export default ErrorMessage
